___CSS_LOADER_EXPORT___.push([module.id, `.image-carousel-container {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  width: fit-content;
  max-width: 100%;
}

.image-carousel-frame {
  overflow: hidden;
  width: calc(5.4 * 12rem); /* Show 3 items at once */
}

.image-carousel-slides {
  display: flex;
  flex-wrap: nowrap;
}

.date-container {
  flex: 0 0 12rem;
  height: 6rem;
  margin: 0.5rem;
  background-color: var(--bg-color);
  border-radius: 1rem;
  border: 1px solid black;
  min-width: 7rem;
  max-width: 12rem;
  margin: 0.5rem;
  padding: 0.25rem;
  box-shadow: 0.5rem 1rem 1rem rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
}
.image-carousel-nav-dot-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 10%;
  width: 45%;
  top: 110%;
  left: 30%;
  position: absolute;
  z-index: 50;
  transition: transform 0.3s ease;
}

.image-carousel-svg-container {
  width: 0.8rem;
  height: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-carousel-dots-svg {
  width: 100%;
  height: 100%;
}

.image-carousel-dots-svg circle {
  fill: rgba(255, 255, 255, 0.5);
  stroke: rgba(45, 45, 45, 0.9);
  transition: stroke-width 0.3s ease;
}

.image-carousel-svg-container.active .image-carousel-dots-svg circle {
  fill: rgba(45, 45, 45, 0.5);
  stroke: rgba(205, 205, 205, 0.9);
}

@keyframes next {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes previous {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(100%);
  }
}

.future-high-low-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  font-style: italic;
}

.name-of-day {
  height: 30%;
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.4);
  color: var(--secondary-color);
}

.future-data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
}

.future-high-low-container > * {
  padding: 0.25rem;
  font-style: italic;
}

.future-data > * {
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.2);
}

.future-data > span {
  color: red;
  font-size: 2rem;
  padding: 0.25rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  box-sizing: border-box;
}
