:root {
  --bg-color: #113344;
  --header-color: floralwhite;
  --detail-color: white;
  --text-color: linear-gradient(
    to right,
    var(--heat-value) 0%,
    var(--cool-value) 100%
  );
  --secondary-color: rgba(255, 255, 255, 1);
  --cool-gradient: linear-gradient(to right, #30cfd0 0%, #330867 100%);
  --adaptive-color: rgba(255, 255, 255, 0);
  --bg-color: rgba(0, 0, 0, 0.3);
}

.pontano-sans-temp {
  font-family: "Pontano Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  background: var(--text-color);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

tinos-regular {
  font-family: "Tinos", serif;
  font-weight: 400;
  font-style: normal;
}

.tinos-bold {
  font-family: "Tinos", serif;
  font-weight: 700;
  font-style: normal;
}

.tinos-regular-italic {
  font-family: "Tinos", serif;
  font-weight: 400;
  font-style: italic;
}

.tinos-bold-italic {
  font-family: "Tinos", serif;
  font-weight: 700;
  font-style: italic;
}

.pontano-sans {
  font-family: "Pontano Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  color: var(--heat-value);
}

body {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 200vh;
  grid-template-rows: 50% 50%;
}

#main-content-container {
  width: 100%;
  height: 50%;
  overflow: hidden;
  display: grid;
  grid-template-rows: 0.2fr 2.5fr 1fr 0.75fr;
}

#content-container > * {
  display: flex;
}

#header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--header-color);
  box-shadow: 0.01rem 0.01rem 0.5rem black;
  padding: 0.5rem;
}

#search-container {
  display: flex;
  width: 50%;
  justify-content: space-between;
  align-items: center;
  border: none;
  border-radius: 0.9rem;
  width: 1;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 3rem;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
  padding: 0.25rem;
}

#search-bar {
  display: flex;
  width: 80%;
  height: 100%;
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
}

#search-bar:focus {
  display: flex;
  outline: none;
  border: none;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
}

#search-btn {
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  width: 20%;
  height: 100%;
}

#search-btn:focus {
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  width: 20%;
  height: 100%;
  outline: none;
  border: none;
}

/* Make sure this container allows absolute children to break out */
.error-container {
  position: relative; /* Must be relative or absolute */
  z-index: 13;
  width: fit-content;
  transition: opacity 0.5s ease-in-out;
}

/* The main bubble */
.search-error {
  position: absolute; /* must be absolute to float over things */
  top: 0;
  left: 0;
  z-index: 13;
  background-color: white;
  color: black;
  border: 1px solid black;
  border-radius: 1.5rem;
  padding: 0.25rem 0.25rem;
  text-align: center;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  left: 27%;
  top: 5.5%;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

#main-container {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-self: center;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  width: 70%;
  height: 95%;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
  padding: 1rem;
  border: 2px solid black;
  transition: transform 0.3s ease;
}

#main-weather-container {
  display: flex;
  flex-direction: row;
  justify-self: center;
  align-self: center;
  width: 100%;
  height: 100%;
}

#title-container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  width: 5rem;
  border-radius: 1rem;
  height: 20%;
  background-color: var(--adaptive-color);
}

#main-icon-container {
  display: flex;
  justify-self: flex-end;
  align-self: center;
  margin-left: 1rem;
}

#main-icon > svg {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 3rem;
  fill: var();
}

#title {
  text-shadow: 0.7rem 0.7rem 2.5rem black;
  color: var(--secondary-color);
  white-space: nowrap;
}

#left-main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
}

#main-temp-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 90%;
  height: 80%;
}

#current-temp-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 90%;
  border-radius: 1rem;
  background-color: var(--adaptive-color);
  margin: 0.5rem;
}

#main-temp {
  font-size: 7rem;
  text-shadow: 0.7rem 0.7rem 5rem black;
  transition:
    transform 0.5s ease,
    filter 0.5s ease;
}

#main-max-min-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 90%;
  transition: transform 0.3s ease;
}

#main-high,
#main-low {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.5rem;
  border-radius: 1rem;
  width: 90%;
  height: 75%;
  margin: 0.5rem;
  background-color: var(--adaptive-color);
  text-shadow: 0.7rem 0.7rem 5rem black;
  font-style: italic;
  transition:
    transform 0.5s ease,
    filter 0.5s ease;
}

#description-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  width: 50%;
  border-radius: 1rem;
  height: 20%;
  /* border: 1px solid black; */
  background-color: var(--adaptive-color);
}

#main-description {
  text-shadow: 0.7rem 0.7rem 2.5rem black;
  color: white;
}

#right-main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
}

#weather-type-gif {
  display: block;
  width: 25rem;
  border-radius: 30px;
  box-shadow: 0.7rem 0.7rem 2.5rem;
}

#data-container {
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90%;
  border: 1px transparent black;
  background-color: var(--header-color);
  box-shadow: 0.01rem 0.01rem 0.2rem black;
  transition: transform 0.3s ease;
}

#left-detail-container,
#middle-detail-container,
#right-detail-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 100%;
  border-radius: 1rem;
  margin: 0.25rem;
}

.top-detail-container,
.bottom-detail-container,
.small-detail-container,
.large-detail-container {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
}

.top-detail-container > *,
.bottom-detail-container > *,
.small-detail-container > *,
.large-detail-container > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  text-align: center;
}

.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  padding: 0.3rem;
  border-radius: 0.9rem;
  padding: 1rem;
  font-size: 1.5rem;
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.6);
  color: var(--detail-color);
}

.detail-container {
  margin: 0.25em;
  height: 90%;
  width: 90%;
  padding: 1rem;
}

.detail-container > * {
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.5);
  color: var(--detail-color);
}

#horizontal-detail-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.small-detail-container {
  display: flex;
  flex-direction: column;
}

#rain-chance-container,
#gusts-container {
  width: 40%;
}

#right-container > .bottom-detail-container {
  display: flex;
  justify-self: flex-start;
  align-self: flex-start;
}

#gif-display {
  display: flex;
  justify-self: flex-end;
  align-self: center;
  height: 3rem;
  width: auto;
}

#bottom-container {
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hidden {
  display: none;
}

[above="true"] {
  filter: drop-shadow(1rem 0.7rem 1rem black);
}

#secondary-container {
  height: 50%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

#info-container {
  display: flex;
  flex-direction: column;
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  height: 60%;
  width: 90%;
  background-color: rgba(0, 0, 0, 0.3);
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
  margin-top: 10%;
  margin-bottom: 15%;
}

#info-header {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.3rem;
  width: 100%;
  padding: 0.3rem;
  padding: 1rem;
  font-size: 3rem;
  color: white;
  font-style: bold;
  text-shadow: 0.3rem 0.3rem 0.3rem black;
  background-color: rgba(0, 0, 0, 0.3);
}

#info-span {
  display: flex;
  justify-content: center;
  align-items: center;
  text-indent: 1rem;
  width: 100%;
  padding: 0.3rem;
  border-radius: 0.9rem;
  padding: 1rem;
  font-size: 2rem;
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.6);
  margin-top: 1rem;
  color: var(--detail-color);
  transition:
    transform 0.5s ease,
    filter 0.5s ease;
}

#links {
  display: flex;
  justify-self: flex-end;
  align-self: flex-end;
  justify-content: center;
  align-items: center;
  height: 10%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
}

#link-container {
  display: flex;
  width: 80%;
  justify-content: space-between;
  align-items: center;
}

.link-img {
  display: flex;
  width: 3rem;
  height: 3rem;
  transition:
    transform 0.5s ease,
    filter 0.5s ease;
}

#linkedin {
  width: 5.5rem;
  height: 4rem;
}

#instagram {
  margin-top: 0.25rem;
}
