nt-container > * {\n  display: flex;\n}\n\n#header {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--header-color);\n  box-shadow: 0.01rem 0.01rem 0.5rem black;\n  padding: 0.5rem;\n}\n\n#search-container {\n  display: flex;\n  width: 50%;\n  justify-content: space-between;\n  align-items: center;\n  border: none;\n  border-radius: 0.9rem;\n  width: 1;\n  background-color: rgba(255, 255, 255, 0.8);\n  border-radius: 3rem;\n  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.2);\n  overflow: hidden;\n  padding: 0.25rem;\n}\n\n#search-bar {\n  display: flex;\n  width: 80%;\n  height: 100%;\n  background-color: transparent;\n  border: none;\n  padding: 0.5rem;\n  font-size: 0.9rem;\n  border-right: 1px solid rgba(0, 0, 0, 0.3);\n}\n\n#search-bar:focus {\n  display: flex;\n  outline: none;\n  border: none;\n  border-right: 1px solid rgba(0, 0, 0, 0.3);\n}\n\n#search-btn {\n  background-color: transparent;\n  border: none;\n  padding: 0.5rem;\n  font-size: 0.9rem;\n  width: 20%;\n  height: 100%;\n}\n\n#search-btn:focus {\n  background-color: transparent;\n  border: none;\n  padding: 0.5rem;\n  font-size: 0.9rem;\n  width: 20%;\n  height: 100%;\n  outline: none;\n  border: none;\n}\n\n/* Make sure this container allows absolute children to break out */\n.error-container {\n  position: relative; /* Must be relative or absolute */\n  z-index: 13;\n  width: fit-content;\n  transition: opacity 0.5s ease-in-out;\n}\n\n/* The main bubble */\n.search-error {\n  position: absolute; /* must be absolute to float over things */\n  top: 0;\n  left: 0;\n  z-index: 13;\n  background-color: white;\n  color: black;\n  border: 1px solid black;\n  border-radius: 1.5rem;\n  padding: 0.25rem 0.25rem;\n  text-align: center;\n  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);\n  left: 27%;\n  top: 5.5%;\n  opacity: 0;\n  transition: opacity 0.5s ease-in-out;\n}\n\n#main-container {\n  display: flex;\n  flex-direction: column;\n  justify-self: center;\n  align-self: center;\n  justify-content: center;\n  align-items: center;\n  padding: 0.25rem;\n  width: 70%;\n  height: 95%;\n  background-color: rgba(0, 0, 0, 0.3);\n  border-radius: 1rem;\n  padding: 1rem;\n  border: 2px solid black;\n  transition: transform 0.3s ease;\n}\n\n#main-weather-container {\n  display: flex;\n  flex-direction: row;\n  justify-self: center;\n  align-self: center;\n  width: 100%;\n  height: 100%;\n}\n\n#title-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 2.5rem;\n  width: 5rem;\n  border-radius: 1rem;\n  height: 20%;\n  background-color: var(--adaptive-color);\n}\n\n#main-icon-container {\n  display: flex;\n  justify-self: flex-end;\n  align-self: center;\n  margin-left: 1rem;\n}\n\n#main-icon > svg {\n  display: flex;\n  justify-content: center;\n  align-content: center;\n  width: 3rem;\n  fill: var();\n}\n\n#title {\n  text-shadow: 0.7rem 0.7rem 2.5rem black;\n  color: var(--secondary-color);\n  white-space: nowrap;\n}\n\n#left-main-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 70%;\n}\n\n#main-temp-container {\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n  width: 90%;\n  height: 80%;\n}\n\n#current-temp-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 70%;\n  height: 90%;\n  border-radius: 1rem;\n  background-color: var(--adaptive-color);\n  margin: 0.5rem;\n}\n\n#main-temp {\n  font-size: 7rem;\n  text-shadow: 0.7rem 0.7rem 5rem black;\n  transition:\n    transform 0.5s ease,\n    filter 0.5s ease;\n}\n\n#main-max-min-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 30%;\n  height: 90%;\n  transition: transform 0.3s ease;\n}\n\n#main-high,\n#main-low {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 3.5rem;\n  border-radius: 1rem;\n 